import React from "react";
import "./Footer.scss";
import { Modal } from "../Modal/Modal";

export class Footer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      value: "",
      isModalOpen: false,
      inputClass: "footer__subscribe-input",
    };
  }
  componentDidUpdate(prevProps) {
    if (this.props.isModalOpen !== prevProps.isModalOpen) {
      this.setState({ isModalOpen: this.props.isModalOpen });
    }
  }
  // subscribe(email) {
  //   fetch(API_URL, {
  //     method: "POST",
  //     headers: {
  //       "Content-Type": "application/json;charset=utf-8",
  //     },
  //     body: JSON.stringify(email)
  //   });
  // }
  triggerModal() {
    const { isModalOpen } = this.state;
    this.setState({ isModalOpen: !isModalOpen });
  }
  wrongInput() {
    const str = this.state.inputClass + " footer__subscribe-input-wrong";
    this.setState({ value: "WRONG INPUT", inputClass: str });
  }
  render() {
    return (
      <footer className="footer">
        <h3 className="footer__title">Subscribe to My Newsletter</h3>
        <form
          className="footer__subscribe"
          onSubmit={(e) => {
            e.preventDefault();
            if (this.state.value) {
              if (this.state.value.slice(-10) === "@gmail.com") {
                this.setState({ isModalOpen: true });
              } else {
                this.wrongInput();
              }
            }
          }}
        >
          <input
            className={this.state.inputClass}
            type="text"
            name="email"
            placeholder="Enter your email here*"
            value={this.state.value}
            onChange={(e) => this.setState({ value: e.target.value })}
            onFocus={() =>
              this.setState({
                value: "",
                inputClass: "footer__subscribe-input",
              })
            }
          />
          <button className="footer__subscribe-btn" type="submit">
            Subscribe Now
          </button>
        </form>
        <div className="footer__signature">
          <p> © 2023 by Hanna Bakes. Proudly created by Maksym Bordonos</p>
        </div>
        <Modal
          isOpen={this.state.isModalOpen}
          onClose={() => {
            this.triggerModal();
            if (this.props.onClose) {
              this.props.onClose();
            }
          }}
        />
      </footer>
    );
  }
}
