import React, { useEffect } from "react";
import "./OrderCelebration.scss";
import { ModalOrder } from "../../../ModalOrder/ModalOrder";
export class OrderCelebration extends React.Component {
  constructor() {
    super();
    this.state = {
      isModalOpen: false,
      selectedItem: {
        img: undefined,
        name: undefined,
        price: undefined,
        quantity: undefined,
      },
    };
    this.images = [];
    this.imagesContent = [
      {
        name: "Macaroons Celebration Cake",
        price: 70,
      },
      {
        name: "Chocolate Macaroons  Cake",
        price: 80,
      },
      {
        name: "Ice Cream Cone Cake",
        price: 75,
      },
      {
        name: "Crazy Donuts and Meringue Cake",
        price: 90,
      },
      {
        name: "Crazy Golden Chocolate Cake",
        price: 100,
      },
      {
        name: "Marble & Donuts Cake",
        price: 90,
      },
    ];
    this.importImg();
  }
  importImg() {
    for (let key in images) {
      this.images.push(images[key].default);
    }
  }
  onModalClose() {
    this.setState({ isModalOpen: false });
  }
  render() {
    return (
      <div className="order-celebration">
        <h2 className="order-celebration__title">Celebration Cakes</h2>
        <p className="order-celebration__text">
          I'm a description. Click me and "Edit Menu" to open the Restaurant
          Menu editor and change my text.
        </p>
        <div className="order-celebration__grid">
          {this.images.map((el, idx) => {
            return (
              <div
                className={`order-celebration__grid-cell order-celebration__grid-cell_${
                  idx + 1
                }`}
                onClick={() => {
                  this.setState({
                    isModalOpen: true,
                    selectedItem: {
                      img: el,
                      name: this.imagesContent[idx].name,
                      price: this.imagesContent[idx].price,
                    },
                  });
                }}
              >
                <img src={el} alt="" className="order-celebration__img" />
                <div className="order-celebration__img-caption">
                  <p className="order-celebration__img-name">
                    {this.imagesContent[idx].name}
                  </p>
                  <p className="order-celebration__img-price">
                    {"$" + this.imagesContent[idx].price}
                  </p>
                </div>
                <p className="order-celebration__grid-cell-icon">+</p>
              </div>
            );
          })}
        </div>
        <ModalOrder
          isOpen={this.state.isModalOpen}
          selectedItem={this.state.selectedItem}
          onClose={() => {
            this.onModalClose();
          }}
          onSubmit={(item) => {
            this.props.onSubmit(item);
          }}
        />
      </div>
    );
  }
}

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => {
    images[item.replace("./", "")] = r(item);
  });
  return images;
}

const images = importAll(
  require.context(
    "../../../../assets/img/order/celebration",
    false,
    /\.(png|jpe?g|svg)$/
  )
);
