import React from "react";
import "./Order.scss";
import {
  BrowserRouter as Router,
  Route,
  NavLink,
  Switch,
} from "react-router-dom";
import { Footer } from "../../Footer/Footer";
import { OrderWedding } from "./OrderWedding/OrderWedding";
import { OrderCelebration } from "./OrderCelebration/OrderCelebration";
import { Accordion, AccordionSummary } from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export class Order extends React.Component {
  constructor() {
    super();
    this.state = {
      accordionExpanded: false,
      request: false,
      cart: [],
      cartSize: 0,
      subtotal: 0,
    };
  }
  getAmountOfItems() {
    return this.state.cartSize === 1
      ? "(1 item)"
      : `(${this.state.cartSize} items)`;
  }
  getSubtotal(item) {
    this.setState({
      subtotal: this.state.subtotal + item.quantity * item.price,
    });
  }
  getCart() {
    if (!this.state.cartSize) {
      return (
        <div>
          <svg
            className="order__aside-right-cart-svg"
            width="53px"
            height="60px"
            viewBox="0 0 53 60"
          >
            <g>
              <path d="M47.5,15.6 C47.5,13.5 45.8,11.9 43.7,11.9 L35.5,11.9 C35.4,8.9 34.8,5.5 32.9,3.1 C31.2,1.1 28.9,-2.22044605e-15 25.9,-2.22044605e-15 C23.4,-2.22044605e-15 21.3,1 19.8,2.8 C17.7,5.2 16.9,8.9 16.7,11.9 L8.9,11.9 C6.8,11.9 5.1,13.5 5.1,15.6 L0,56.1 C0,58.2 1.7,59.9 3.8,59.9 L26.9,59.9 L48.8,59.9 C50.9,59.9 52.6,58.2 52.6,56.1 L47.5,15.6 L47.5,15.6 Z M21.2,4 C22.4,2.7 23.9,2 25.8,2 C28.2,2 30,2.9 31.3,4.4 C32.9,6.4 33.4,8.8 33.5,11.8 L18.6,11.8 C18.8,8.9 19.5,6 21.2,4 L21.2,4 Z M48.8,57.9 L26.9,57.9 L3.8,57.9 C2.8,57.9 2.1,57.1 2,56.2 L7.2,15.8 L7.3,15.7 L7.3,15.6 C7.3,14.6 7.9,14 8.9,14 L16.5,14 C16.5,16 16.7,17.3 16.7,17.4 C16.8,17.9 17.2,18.2 17.7,18.2 L17.8,18.2 C18.3,18.1 18.7,17.8 18.7,17.2 C18.7,17.2 18.5,16 18.6,14 L33.6,14 C33.5,16 33.3,17.1 33.3,17.1 C33.2,17.6 33.5,18.1 34.1,18.2 C34.6,18.3 35.2,18.1 35.3,17.5 C35.3,17.4 35.6,16 35.7,14 L43.9,14 C44.9,14 45.5,14.5 45.5,15.6 L45.5,15.7 L45.6,15.8 L50.7,56.2 C50.5,57.1 49.8,57.9 48.8,57.9 L48.8,57.9 Z"></path>
            </g>
          </svg>
          <p className="order__aside-right-cart-caption">
            Browse our menu and start adding items to your order
          </p>
        </div>
      );
    }
    const cart = [];
    for (let i = 0; i < this.state.cart.length; i++) {
      const { quantity, name, price } = this.state.cart[i];
      cart.push(
        <div className="order__aside-right-cart-item">
          <p className="order__aside-right-cart-item-name">
            {quantity}x{name}
          </p>
          <p className="order__aside-right-cart-item-price">${price}</p>
          <div className="order__aside-right-cart-btns">
            <button
              className="order__aside-right-cart-btn"
              onClick={() => this.addItem(name)}
            >
              +
            </button>
            <button
              className="order__aside-right-cart-btn"
              onClick={() => this.removeItem(name)}
            >
              x
            </button>
          </div>
        </div>
      );
    }
    return cart;
  }
  addItem(name) {
    this.setState({ cartSize: this.state.cartSize + 1 });
    const newArr = this.state.cart.map((el) => {
      if (el.name === name) {
        el.quantity++;
        this.setState({ subtotal: this.state.subtotal + el.price });
        return el;
      }
      return el;
    });
    this.setState({ cart: newArr });
  }
  removeItem(name) {
    let allowFilter = false;
    // if (this.state.cart.some((el) => el.name === name)) {
    this.setState({ cartSize: this.state.cartSize - 1 });
    let newArr = this.state.cart.map((el) => {
      if (el.name === name) {
        el.quantity--;
        if (el.quantity === 0) {
          allowFilter = true;
        }
        this.setState({ subtotal: this.state.subtotal - el.price });
        return el;
      }
      return el;
    });
    if (allowFilter) {
      newArr = newArr.filter((el) => el.quantity > 0);
    }
    console.log(newArr);
    this.setState({ cart: newArr });
  }
  toggleRequestForm() {
    if (this.state.request) {
      return (
        <textarea
          className="order__aside-right-form-input"
          name="request"
          id=""
          cols="25"
          rows="3"
          onBlur={() => this.setState({ request: false })}
          onSubmit={(e) => {
            return e.preventDefault();
          }}
          placeholder="Special request"
        ></textarea>
      );
    } else {
      return (
        <button
          className="order__aside-right-form-btn-request"
          onClick={() => this.setState({ request: true })}
        >
          + Special request
        </button>
      );
    }
  }
  toggleAccordion() {
    this.setState({ accordionExpanded: !this.state.accordionExpanded });
  }
  addToCart(item) {
    this.setState({ cart: [...this.state.cart, item] });
    this.setState({ cartSize: this.state.cartSize + item.quantity });
    this.getSubtotal(item);
  }
  render() {
    return (
      <div>
        <div className="order">
          <h1 className="order__title">Order</h1>
          <div className="order__container">
            <Router>
              <div className="order__aside-left">
                <Accordion
                  expanded={this.state.accordionExpanded}
                  onClick={() => this.toggleAccordion()}
                >
                  <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panel1a-content"
                    id="panel1a-header"
                  >
                    CAKES MENU
                  </AccordionSummary>
                  <div className="order__accordion-content">
                    <NavLink
                      to="/order/wedding"
                      className="order__btn-option"
                      activeClassName="order__btn-option_selected"
                      onClick={() => this.toggleAccordion()}
                    >
                      Wedding Cakes
                    </NavLink>
                    <NavLink
                      to="/order/celebration"
                      className="order__btn-option"
                      activeClassName="order__btn-option_selected"
                      onClick={() => this.toggleAccordion()}
                    >
                      Celebration Cakes
                    </NavLink>
                  </div>
                </Accordion>
              </div>
              <div className="order__menu">
                <div className="order__menu-header">
                  <h2 className="order__menu-title">CAKES MENU</h2>
                  <p className="order__menu-timing">
                    Served daily between 12-5pm
                  </p>
                </div>
                <Switch>
                  <Route exact path="/order/wedding">
                    <OrderWedding onSubmit={(item) => this.addToCart(item)} />
                  </Route>
                  <Route path="/order/celebration">
                    <OrderCelebration
                      onSubmit={(item) => this.addToCart(item)}
                    />
                  </Route>
                </Switch>
              </div>
              <div className="order__aside-right">
                <div className="order__aside-right-header">
                  <p className="order__aside-right-header-text">My Order</p>
                  <p className="order__aside-right-header-text">
                    {this.getAmountOfItems()}
                  </p>
                </div>
                <div className="order__aside-right-cart">{this.getCart()}</div>
                <hr className="order__aside-right-divider" />
                <div className="order__aside-right-subtotal order__aside-right-header">
                  <p className="order__aside-right-header-text">Subtotal</p>
                  <p className="order__aside-right-header-text">
                    {"$" + this.state.subtotal}
                  </p>
                </div>
                <form className="order__aside-right-form">
                  {this.toggleRequestForm()}
                  <p className="order__aside-right-form-caption">
                    Online ordering unavailable
                  </p>
                  <button
                    className="order__aside-right-form-btn-submit"
                    type="button"
                  >
                    ORDER NOW &rsaquo;
                  </button>
                </form>
              </div>
            </Router>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}
