import React from "react";
import "./ModalOrder.scss";

export class ModalOrder extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quantity: 1,
    };
  }
  onOuterClick(e) {
    if (e.target === e.currentTarget) {
      this.setState({ quantity: 1 });
      this.props.onClose();
    }
  }
  submitHandler() {
    const updateItem = this.props.selectedItem;
    updateItem.quantity = this.state.quantity;
    this.setState({ quantity: 1 });
    this.props.onClose();
    this.props.onSubmit(updateItem);
  }
  render() {
    if (!this.props.isOpen) {
      return null;
    }
    return (
      <div className="order__modal" onClick={(e) => this.onOuterClick(e)}>
        <div className="order__modal-container">
          <button
            onClick={() => this.props.onClose()}
            className="order__modal-btn"
          >
            X
          </button>
          <img
            src={this.props.selectedItem.img}
            alt="img"
            className="order__modal-img"
          />
          <div className="order__modal-wrapper">
            <h2 className="order__modal-name">
              {this.props.selectedItem.name}
            </h2>
            <p className="order__modal-price">
              {"$" + this.props.selectedItem.price}
            </p>
            <p className="order__modal-text">Special requests?</p>
            <hr className="order__modal-divider" />
            <form
              className="order__modal-form"
              onSubmit={(e) => {
                e.preventDefault();
                this.submitHandler();
              }}
            >
              <textarea
                className="order__modal-form-text"
                name="extra-info"
                placeholder="+ Add them here. We'll do our best to make it happen"
                id=""
                cols="30"
                rows="3"
              ></textarea>
              <p className="order__modal-form-warning">
                * Extra charges may apply
              </p>
              <div className="order__modal-form-quantity">
                <p className="order__modal-form-quantity-title">Quantity</p>
                <div className="order__modal-form-quantity-btns">
                  <button
                    className="order__modal-form-quantity-btn"
                    type="button"
                    onClick={() => {
                      if (this.state.quantity > 1) {
                        this.setState({ quantity: this.state.quantity - 1 });
                      }
                    }}
                  >
                    -
                  </button>
                  <p className="order__modal-form-quantity-reflect">
                    {this.state.quantity}
                  </p>
                  <button
                    className="order__modal-form-quantity-btn"
                    type="button"
                    onClick={() =>
                      this.setState({ quantity: this.state.quantity + 1 })
                    }
                  >
                    +
                  </button>
                </div>
              </div>
              <button className="order__modal-form-btn" type="submit">
                + ADD TO MY ORDER
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
