import React from "react";
import "./About.scss";
import { Footer } from "../../Footer/Footer";
export class About extends React.Component {
  render() {
    return (
      <div>
        <div className="about">
          <h1 className="about__title">About Me</h1>
          <hr className="about__divider" />
          <h3 className="about__title-2">
            HANNA MASON - PASTRY CHEF, HOME BAKER, BLOGGER
          </h3>
          <div className="about__img"></div>
          <p className="about__text">
            Click here to add your own text and edit me. It’s easy. Just click
            “Edit Text” or double click me to add your own content and make
            changes to the font. Feel free to drag and drop me anywhere you like
            on your page. I’m a great place for you to tell a story and let your
            users know a little more about you.
          </p>
          <p className="about__text">
            This is a great space to write long text about your company and your
            services. You can use this space to go into a little more detail
            about your company. Talk about your team and what services you
            provide. Tell your visitors the story of how you came up with the
            idea for your business and what makes you different from your
            competitors. Make your company stand out and show your visitors who
            you are.
          </p>
        </div>
        <Footer />
      </div>
    );
  }
}
