import React from "react";
import "./Modal.scss";

export class Modal extends React.Component {
  onOuterClick(e) {
    if (e.target === e.currentTarget) {
      this.props.onClose();
    }
  }
  render() {
    if (!this.props.isOpen) {
      return null;
    }
    return (
      <div className="footer__modal" onClick={(e) => this.onOuterClick(e)}>
        <div className="modal">
          <p className="modal__text">Thank you for subscribing your email🙂</p>
          <button onClick={() => this.props.onClose()} className="modal__btn">
            X
          </button>
        </div>
      </div>
    );
  }
}
