import React from "react";
import "./Home.scss";
import "./grid.scss";
import "./palatino-linotype.ttf";
import cell1 from "../../../assets/img/cake1.jpg";
import cell3 from "../../../assets/img/cake2.jpg";
import cell5 from "../../../assets/img/hanna.jpg";
import cell6 from "../../../assets/img/cake3.jpg";
import { NavLink } from "react-router-dom";
import { Carousel1 } from "../../Carousel1/Carousel1";
import { Footer } from "../../Footer/Footer";

export class Home extends React.Component {
  render() {
    return (
      <div>
        <div className="home">
          <NavLink to="/about" className="home__title">
            HANNA BAKES.
          </NavLink>
          <div className="home__grid">
            <div className="cell-1">
              <img src={cell1} alt="Cake-Image" className="home__img" />
            </div>
            <div className="cell-2">
              <h1 className="home__title-seo">
                Designer Cakes Delivered to Your Doorstep
              </h1>
              <NavLink to="/order/wedding" className="home__btn">
                Order Here
              </NavLink>
            </div>
            <div className="cell-3">
              <img src={cell3} alt="Cake-Image" className="home__img" />
            </div>
            <div className="cell-4">
              <h2 className="home__title-pitch">
                ALL CAKES ARE TAILORED TO YOUR TASTE AND STYLE
              </h2>
            </div>
            <div className="cell-5">
              <img src={cell5} alt="Author-Image" className="home__img" />
              {/* <button className="home__btn home__btn_corner">Meet Hanna</button> */}
              <NavLink to="/about" className="home__btn home__btn_corner">
                Meet Hanna
              </NavLink>
            </div>
            <div className="cell-6">
              <img src={cell6} alt="Cake-Image" className="home__img" />
              <NavLink to="/contact" className="home__btn home__btn_corner">
                Contact Me
              </NavLink>
            </div>
          </div>
          <h2 className="home__title-follow-me">Follow me on Instagram</h2>
          <Carousel1 />
        </div>
        <Footer />
      </div>
    );
  }
}
