import React, { useEffect } from "react";
import "./Content.scss";
import {
  BrowserRouter as Router,
  Route,
  NavLink,
  useLocation,
  withRouter,
  Redirect,
} from "react-router-dom";
import { AnimatedSwitch } from "react-router-transition";
import { Home } from "../Pages/Home/Home";
import { About } from "../Pages/About/About";
import { Order } from "../Pages/Order/Order";
import { Contact } from "../Pages/Contact/Contact";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFacebookF,
  faTwitter,
  faPinterestP,
  faInstagram,
} from "@fortawesome/free-brands-svg-icons";

export class Content extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      classRouter: "header__router",
      classSocial: "header__social",
      classNavToggle: "header__nav-toggle",
      navOpen: false,
    };
  }
  navToggle() {
    if (this.state.navOpen) {
      this.setState({
        classRouter: "header__router",
        classSocial: "header__social",
        classNavToggle: "header__nav-toggle",
        navOpen: false,
      });
    } else
      this.setState({
        classRouter: this.state.classRouter + " header__router_active",
        classSocial: this.state.classSocial + " header__social_active",
        classNavToggle:
          this.state.classNavToggle + " header__nav-toggle_active",
        navOpen: true,
      });
  }
  render() {
    return (
      <Router>
        <ScrollToTop>
          <header className="header">
            <nav className="header__nav">
              <ul className={this.state.classRouter}>
                <li className="header__router-link">
                  <NavLink
                    to="/home"
                    activeClassName="header__router-link_selected"
                    onClick={() => this.navToggle()}
                  >
                    Home
                  </NavLink>
                </li>
                <li className="header__router-link">
                  <NavLink
                    to="/about"
                    activeClassName="header__router-link_selected"
                    onClick={() => this.navToggle()}
                  >
                    About
                  </NavLink>
                </li>
                <li className="header__router-link">
                  <NavLink
                    to="/order/wedding"
                    activeClassName="header__router-link_selected"
                    onClick={() => this.navToggle()}
                  >
                    Order
                  </NavLink>
                </li>
                <li className="header__router-link">
                  <NavLink
                    to="/contact"
                    activeClassName="header__router-link_selected"
                    onClick={() => this.navToggle()}
                  >
                    Contact
                  </NavLink>
                </li>
              </ul>

              <div className={this.state.classSocial}>
                <a
                  href="https://www.facebook.com/"
                  className="header__social-link"
                  title="facebook"
                  target="_blank"
                  rel="noreferrer"
                  onClick={() => this.navToggle()}
                >
                  <FontAwesomeIcon icon={faFacebookF} size="lg" />
                </a>
                <a
                  href="https://www.instagram.com/"
                  className="header__social-link"
                  title="instagram"
                  target="_blank"
                  rel="noreferrer"
                  onClick={() => this.navToggle()}
                >
                  <FontAwesomeIcon icon={faTwitter} size="lg" />
                </a>
                <a
                  href="https://www.twitter.com/"
                  className="header__social-link"
                  title="twitter"
                  target="_blank"
                  rel="noreferrer"
                  onClick={() => this.navToggle()}
                >
                  <FontAwesomeIcon icon={faPinterestP} size="lg" />
                </a>
                <a
                  href="https://www.pinterest.com/"
                  className="header__social-link"
                  title="pinterest"
                  target="_blank"
                  rel="noreferrer"
                  onClick={() => this.navToggle()}
                >
                  <FontAwesomeIcon icon={faInstagram} size="lg" />
                </a>
              </div>
              <button
                className={this.state.classNavToggle}
                onClick={() => this.navToggle()}
              ></button>
            </nav>
          </header>
          <AnimatedSwitch
            atEnter={{ opacity: 0 }}
            atLeave={{ opacity: 0 }}
            atActive={{ opacity: 1 }}
            className="switch-wrapper"
          >
            <Route exact path="/">
              <Redirect to="/home" />
            </Route>
            <Route exact path="/home">
              <Home />
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/order/wedding">
              <Order />
            </Route>
            <Route path="/contact">
              <Contact />
            </Route>
          </AnimatedSwitch>
        </ScrollToTop>
      </Router>
    );
  }
}
function _ScrollToTop(props) {
  const { pathname } = useLocation();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);
  return props.children;
}
const ScrollToTop = withRouter(_ScrollToTop);
