import React from "react";
import "./Carousel1.scss";
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
} from "pure-react-carousel";
import "pure-react-carousel/dist/react-carousel.es.css";
import { ModalImgCarousel } from "../ModalImgCarousel/ModalImgCarousel";

export class Carousel1 extends React.Component {
  constructor() {
    super();
    this.state = {
      isModalOpen: false,
      selectedImg: undefined,
      visibleSlides: undefined,
    };
    this.images = [];
    this.importImg();
  }
  importImg() {
    for (let key in images) {
      this.images.push(images[key].default);
    }
  }
  componentDidMount() {
    if (window.matchMedia("(max-width: 574px)").matches) {
      this.setState({ visibleSlides: 1 });
    } else {
      this.setState({ visibleSlides: 8 });
    }
  }
  triggerModal() {
    const { isModalOpen } = this.state;
    this.setState({ isModalOpen: !isModalOpen });
  }
  render() {
    return (
      <CarouselProvider
        naturalSlideWidth={100}
        naturalSlideHeight={100}
        totalSlides={this.images.length}
        visibleSlides={this.state.visibleSlides}
        step={this.state.visibleSlides}
      >
        <Slider>
          {this.images.map((el, idx) => {
            return (
              <Slide index={idx}>
                <div className="carousel__slide-wrapper">
                  <img src={el} alt="img" className="carousel__img" />
                  <div
                    className="carousel__img-hover"
                    onClick={() => {
                      this.setState({ isModalOpen: true, selectedImg: el });
                    }}
                  ></div>
                </div>
              </Slide>
            );
          })}
        </Slider>
        <ButtonBack className={"carousel__btn"}>&lsaquo;</ButtonBack>
        <ButtonNext className={"carousel__btn"}>&rsaquo;</ButtonNext>
        <ModalImgCarousel
          isOpen={this.state.isModalOpen}
          img={this.state.selectedImg}
          onClose={() => {
            this.triggerModal();
          }}
        />
      </CarouselProvider>
    );
  }
}

function importAll(r) {
  let images = {};
  r.keys().map((item, index) => {
    images[item.replace("./", "")] = r(item);
  });
  return images;
}
const images = importAll(
  require.context("../../assets/img/Carousel", false, /\.(png|jpe?g|svg)$/)
);
