import React from "react";
import "./ModalImgCarousel.scss";

export class ModalImgCarousel extends React.Component {
  onOuterClick(e) {
    if (e.target === e.currentTarget) {
      this.props.onClose();
    }
  }
  render() {
    if (!this.props.isOpen) {
      return null;
    }
    return (
      <div className="carousel__modal" onClick={(e) => this.onOuterClick(e)}>
        <div className="carousel__modal-container">
          <img src={this.props.img} alt="img" className="carousel__modal-img" />
          <div className="carousel__modal-text">
            <h2 className="carousel__modal-cake-id">Cake id8345</h2>
            <p className="carousel__modal-description">
              Here will be cake description...
            </p>
          </div>
          <button
            onClick={() => this.props.onClose()}
            className="carousel__modal-btn"
          >
            X
          </button>
        </div>
      </div>
    );
  }
}
